import React, { useRef, useState, useEffect } from "react";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  Grid,
  Popper,
  TextField,
  Typography,
} from "@mui/material";
import { useFormik } from "formik";
import { makeStyles } from "@mui/styles";
import { motion, AnimateSharedLayout } from "framer-motion";
import { HexColorPicker } from "react-colorful";
import * as yup from "yup";
import CloseButtonWrapper from "components/Common/Dialog/CloseButtonWrapper";

const useStyles = makeStyles((theme) => ({
  dmGroupColorContainer: {
    marginTop: 8,
    "&>div": {
      display: "flex",
      justifyContent: "flex-start",
      alignItems: "flex-start",
      "&>p": {
        marginLeft: theme.spacing(1),
        padding: 6,
      },
      "&>div": {
        display: "flex",
        justifyContent: "flex-start",
        alignItems: "center",
        marginTop: "2px",
        flexWrap: "wrap",
      },
    },
  },
  dmGroupColorCircle: {
    display: "block",
    width: "20px",
    height: "20px",
    borderRadius: "50%",
    margin: "5px",
    position: "relative",
    cursor: "pointer",
    flexShrink: "0",
  },
  dmSelectedGroupColor: {
    position: "absolute",
    top: "-6px",
    left: "-6px",
    right: "-6px",
    bottom: "-6px",
    border: "3px solid white",
    borderRadius: "50%",
  },
  dmCustomColorPickerPopper: {
    padding: 10,
    boxShadow: theme.shadows[5],
    borderRadius: theme.shape.borderRadius,
    zIndex: theme.zIndex.modal + 1,
    backgroundColor: "white",
    "&>button": {
      marginTop: theme.spacing(1),
    },
  },
  dmCustomColorAction: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    borderBottom: "unset !important",
    marginTop: theme.spacing(1),
    width: "100% !important",
    "&>button": {
      margin: theme.spacing(0.5),
    },
  },
}));

const spring = {
  type: "spring",
  stiffness: 500,
  damping: 30,
};

const createGroupSchema = yup.object().shape({
  groupName: yup.string().required("Required"),
});

export default function CreateGroup(props) {
  const { open, handleClose } = props;
  const classes = useStyles();

  useEffect(() => {
    if (open) {
    } else {
      formik.resetForm();
    }
    // eslint-disable-next-line
  }, [open]);

  const formik = useFormik({
    initialValues: {
      groupName: "",
    },
    onSubmit: (values) => {
      alert(JSON.stringify(values, null, 2));
    },
    validationSchema: createGroupSchema,
  });

  const [colors, setColors] = useState([
    "#ff0055",
    "#0099ff",
    "#22cc88",
    "#ffaa00",
  ]);

  const [selectedColor, setSelectedColor] = useState(colors[0]);
  const [showColorSelectionPopper, setShowColorSelectionPopper] = useState(
    false
  );
  const customColorRef = useRef(null);

  useEffect(() => {
    if (open) {
    } else {
      setShowColorSelectionPopper(false);
    }
  }, [open]);

  const handleAddCustomColor = () => {
    if (!colors.includes(colors)) {
      setColors([...colors, selectedColor]);
    }
    setShowColorSelectionPopper(false);
  };

  return (
    <Dialog open={open} onClose={handleClose}>
      <CloseButtonWrapper onClose={handleClose}>Add Group</CloseButtonWrapper>
      <DialogContent>
        <Grid>
          <form id="create-group-from">
            <>
              <TextField
                fullWidth
                placeholder="Group name"
                label="Group name"
                name="groupName"
                value={formik.values.groupName}
                onChange={formik.handleChange}
                onBlur={formik.handleBlur}
                helperText={formik.touched.groupName && formik.errors.groupName}
                error={
                  formik.touched.groupName && Boolean(formik.errors.groupName)
                }
              />
              <Grid className={classes.dmGroupColorContainer}>
                <Typography variant="body2">Color</Typography>
                <Grid>
                  <Grid>
                    <AnimateSharedLayout>
                      {colors.map((color) => (
                        <Item
                          key={color}
                          color={color}
                          isSelected={selectedColor === color}
                          onClick={() => setSelectedColor(color)}
                        />
                      ))}
                    </AnimateSharedLayout>
                  </Grid>
                  <Typography
                    variant="body2"
                    underline
                    ref={customColorRef}
                    onClick={() => setShowColorSelectionPopper(true)}
                  >
                    custom
                  </Typography>
                </Grid>
                <Popper
                  anchorEl={customColorRef?.current}
                  open={showColorSelectionPopper}
                  className={classes.dmCustomColorPickerPopper}
                  onClose={() => {
                    console.log("Hello");
                  }}
                >
                  <HexColorPicker
                    color={selectedColor}
                    onChange={setSelectedColor}
                  />
                  <Grid className={classes.dmCustomColorAction}>
                    <Button
                      fullWidth
                      onClick={() => {
                        setShowColorSelectionPopper(false);
                      }}
                      variant="outlined"
                    >
                      Cancel
                    </Button>
                    <Button fullWidth onClick={handleAddCustomColor}>
                      Add
                    </Button>
                  </Grid>
                </Popper>
              </Grid>
            </>
          </form>
        </Grid>
      </DialogContent>
      <DialogActions>
        <Button size="small" variant="string" onClick={handleClose}>
          Cancel
        </Button>
        <Button
          size="small"
          onClick={formik.handleSubmit}
          form="create-group-from"
          type="submit"
        >
          Create
        </Button>
      </DialogActions>
    </Dialog>
  );
}

function Item({ color, isSelected, onClick }) {
  const classes = useStyles();

  return (
    <span
      className={classes.dmGroupColorCircle}
      onClick={onClick}
      style={{ backgroundColor: color }}
    >
      {isSelected && (
        <motion.div
          layoutId="outline"
          className={classes.dmSelectedGroupColor}
          initial={false}
          animate={{ borderColor: color }}
          transition={spring}
        />
      )}
    </span>
  );
}
