import {
  Avatar,
  Button,
  Chip,
  Divider,
  Grid,
  IconButton,
  Paper,
  Switch,
  TextField,
  Typography,
} from "@mui/material";
import HeaderWrapper from "components/Wrapper/HeaderWrapper";
import React, { useState } from "react";
import { makeStyles } from "@mui/styles";
import { ReactComponent as PencilIcon } from "assets/PencilIcon.svg";
import CreateGroup from "components/Profile/CreateGroup";
import { useFormik } from "formik";
import * as yup from "yup";
import { ReactComponent as PushNotificationIcon } from "assets/PushNotificationIcon.svg";
import { ReactComponent as MainNotificationIcon } from "assets/MainNotificationIcon.svg";
import { ReactComponent as WhatsappNotificationIcon } from "assets/WhatsappNotificationIcon.svg";

const useStyles = makeStyles((theme) => ({
  dmProfileRoot: {
    height: "100%",
    padding: theme.spacing(1),
    paddingTop: theme.spacing(2),
    "&>div": {
      marginTop: 10,
    },
  },
  dmUserProfileInfo: {
    padding: theme.spacing(1.5),
    borderRadius: theme.shape.borderRadius,
    display: "flex",
    position: "relative",
    marginTop: theme.spacing(1),
    "&>div": {
      "&:nth-child(2)": {
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "flex-start",
        padding: "0px 0px 0px 16px ",
      },
      "&:first-child": {
        width: "100px",
        height: "100px",
      },
    },
  },
  dmEditProfileIcon: {
    position: "absolute !important",
    right: 10,
    top: 10,
  },
  dmGroupContainer: {
    display: "flex",
    flexWrap: "wrap",
    "&>div": {
      margin: theme.spacing(0.5),
    },
    "&>span": {
      width: "30px",
      minWidth: "30px",
      height: "30px",
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      borderRadius: "50%",
      border: `1px solid ${theme.palette.grey[400]}`,
      background: "white",
      cursor: "pointer",
      margin: theme.spacing(0.5),
    },
  },
  dmNotificationContainer: {
    "&>div": {
      display: "flex",
      justifyContent: "space-between",
      alignItems: "center",
      padding: "4px 0px",
      "&>div": {
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        "&>svg": {
          margin: "0px 6px 0px 4px ",
          width: 20,
          height: 20,
        },
      },
    },
  },
  dmEditingProfile: {
    "&>div": {
      marginTop: 4,
      width: "fit-content",
      marginLeft: "auto",
    },
  },
}));

const userProfileSchema = yup.object().shape({
  name: yup.string().required("Required"),
  birthDate: yup.string().required("Required"),
});

export default function Profile() {
  const classes = useStyles();

  const [showGroupDialog, setShowGroupDialog] = useState(false);
  const [editProfile, setEditProfile] = useState(false);

  const formik = useFormik({
    initialValues: {
      name: "Praful Divani",
      birthDate: "19-0-1999",
    },
    onSubmit: (values) => {
      alert(JSON.stringify(values, null, 2));
    },
    validationSchema: userProfileSchema,
  });

  return (
    <HeaderWrapper>
      <Grid className={classes.dmProfileRoot}>
        <Typography variant="h5">Setting</Typography>
        <Paper className={classes.dmUserProfileInfo} elevation={5}>
          <Avatar
            src={`${process.env.PUBLIC_URL}/assets/profile.png`}
            alt="profile-img"
          />
          <Grid>
            {editProfile ? (
              <Grid className={classes.dmEditingProfile}>
                <TextField
                  label="Name"
                  placeholder="Name"
                  size="small"
                  name="name"
                  value={formik.values.name}
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  helperText={formik.touched.name && formik.errors.name}
                  error={formik.touched.name && Boolean(formik.errors.name)}
                />
                <TextField
                  label="Birthdate"
                  placeholder="Birthdate"
                  size="small"
                  name="birthDate"
                  value={formik.values.birthDate}
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  helperText={
                    formik.touched.birthDate && formik.errors.birthDate
                  }
                  error={
                    formik.touched.birthDate && Boolean(formik.errors.birthDate)
                  }
                />
                <Grid>
                  <Button
                    variant="string"
                    size="small"
                    onClick={() => setEditProfile(false)}
                  >
                    Cancel
                  </Button>
                  <Button size="small" onClick={() => setEditProfile(false)}>
                    Save
                  </Button>
                </Grid>
              </Grid>
            ) : (
              <>
                <Typography variant="h5" bold>
                  {formik.values.name}
                </Typography>
                <Typography variant="subtitle1" grey>
                  {formik.values.birthDate}
                </Typography>
              </>
            )}
          </Grid>
          {!editProfile && (
            <IconButton
              className={classes.dmEditProfileIcon}
              onClick={() => setEditProfile(!editProfile)}
            >
              <PencilIcon />
            </IconButton>
          )}
        </Paper>

        <Grid>
          <Typography grey variant="body2">
            Groups
          </Typography>
          <Grid className={classes.dmGroupContainer}>
            {Array.from(new Array(6)).map((group, i) => (
              <Chip
                label={"Group " + (i + 1)}
                variant="outlined"
                onDelete={() => {}}
                deleteIcon={<PencilIcon width="12" />}
                key={i}
              />
            ))}
            <span
              onClick={() => {
                setShowGroupDialog(true);
              }}
            >
              +
            </span>
          </Grid>
        </Grid>

        <Grid>
          <Typography grey variant="body2">
            Notification
          </Typography>
          <Divider />
          <Grid className={classes.dmNotificationContainer}>
            <Grid>
              <Grid>
                <Typography semiBold variant="subtitle1">
                  Reminder
                </Typography>
              </Grid>
              <Switch />
            </Grid>
            <Divider />
            <Grid>
              <Grid>
                <PushNotificationIcon />
                <Typography semiBold variant="subtitle1">
                  Push notification
                </Typography>
              </Grid>
              <Switch />
            </Grid>
            <Divider />
            <Grid>
              <Grid>
                <MainNotificationIcon />
                <Typography semiBold variant="subtitle1">
                  Mail
                </Typography>
              </Grid>
              <Switch />
            </Grid>
            <Divider />
            <Grid>
              <Grid>
                <WhatsappNotificationIcon />
                <Typography semiBold variant="subtitle1">
                  Whatsapp
                </Typography>
              </Grid>
              <Switch />
            </Grid>
            <Divider />
          </Grid>
        </Grid>

        <Grid>
          <Typography grey variant="body2">
            Other thing
          </Typography>
          <Divider />
          <Grid className={classes.dmNotificationContainer}>
            <Grid>
              <Grid>
                <Typography semiBold variant="subtitle1">
                  Remind me for events
                </Typography>
              </Grid>
              <Switch />
            </Grid>
            <Divider />
            <Grid>
              <Grid>
                <Typography semiBold variant="subtitle1">
                  Dark mode
                </Typography>
              </Grid>
              <Switch />
            </Grid>
            <Divider />
          </Grid>
        </Grid>
      </Grid>

      <CreateGroup
        open={showGroupDialog}
        handleClose={() => setShowGroupDialog(false)}
      />
    </HeaderWrapper>
  );
}
